	module.exports = {"data":{"markdownRemark":{"html":"<p>Happy Birthday Mamam!</p>\n<p>Yesterday was my girlfriend's birthday and I was very happy to see her. I came to Makati at around 9:30 PM to avoid getting stuck in traffic. I also have to fight my own demons first, to be able to get there. On my first glance at her face, I knew she's not that happy, maybe because I came late and didn't brought her cake. Can you imagine that? Haha! Good thing I've already given my birthday gift to her which she's wearing. But we all know that cake is very symbolic when it comes to celebrations and I don't have any with me. I know she was mad so I have to make every move right to make her feel good. So we went on a walk and decided to eat near her office. The place is called <a href=\"https://www.facebook.com/carteldeli/\">Cartel</a> and the ambience is nice, I bought sandwich for us and some desserts, we talked a little and I was trying to make her feel good by literally feeding her and it worked! Haha! It's funny how the magic of sugar works.</p>\n<p>After a while she came back to her office and I waited for her to finish work. Then we walked to <a href=\"https://www.zomato.com/manila/la-vie-parisienne-salcedo-village-makati-city\">La Vie</a> and grabbed a glass of wine. It was about to close but we still decided to go. The place really looks nice but smaller compared to their branch in Cebu. After our wine session we walked all the way to EDSA Ayala to head back home. </p>\n<h2>Message</h2>\n<p>I know that you accomplished a lot and for that I tip my hat to you. Achieving an MBA degree is not everyone can do. I'm so proud of you. I know all your adversities in life and you still manage to hang on with faith in God. Your hard work finally paid off and now you have a title on your name. Pretty soon I'll mess up your name by changing your last name! ;-) </p>\n<p>I just want you to know that I'm always here for you no matter what. We will surely take this world challenges together. Our next adventures awaits. I love you so much and please take care of yourself.</p>\n<p>For my final words I'd like to leave you a quote by Ausonius.</p>\n<blockquote>\n<p>Let us never know what old age is. Let us know the happiness time brings, not count the years.</p>\n</blockquote>","frontmatter":{"path":"/birthday-girl","title":"Birthday Girl","date":"October 05, 2017"}}},"pathContext":{}}

